@media screen {
  :root {
    --background-color: #424C6D;
    --border-color: #CE7B5B;
    --accent-color: #FFD378;
    --text-color: #FCFAF1;
    --text-color-accent: #d7d5cc;
    --text-font: "Century Gothic", sans-serif;
  }

  body {
    margin: 0;
    padding: 0;
    background-color: var(--background-color);
    display: grid;
    place-items: center;
    height: 100vh;
  }

  button {
    margin: 0;
    padding: 0;
    border: none;
    outline: none;
    background: none;
  }

  p {
    margin: 0;
    padding: 0;
  }

  ul, ol {
    padding: 0;
    margin: 0;
    list-style: none;
  }

  input {
    border: none;
    outline: none;
  }

  .converter {
    display: grid;
    grid-gap: 2vw;
  }

  .list-input {
    width: 30vw;
    height: 3vw;
    display: grid;
    grid-template-columns: 3fr 7fr;
  }

  .list-input__drop-list {
    grid-area: 1 / 1 / span 1 / span 1;
    position: relative;
  }

  .list-input__drop-list__button {
    background-color: var(--border-color);
    font-family: var(--text-font);
    font-size: 1.3vw;
    width: 100%;
    height: 100%;
    border-radius: 0.5vw 0 0 0.5vw;
    transition: .15s;
    color: var(--text-color);
  }

  .list-input__drop-list__button:active {
    transform: scale(.96);
  }

  .list-input__drop-list__list {
    position: absolute;
    margin-top: 12%;
    width: 100%;
    visibility: hidden;
    opacity: 0;
    transition: .15s;
    box-shadow: 0.1vw 0.1vw 0.4vw -0.1vw dimgray;
    max-height: 15vw;
    overflow-y: auto;
    border-radius: 0.4vw;
    z-index: 1;
  }

  .list-input__drop-list__list__item {
    background-color: white;
    color: #333333;
    display: flex;
    justify-content: center;
    align-items: center;
    font-family: var(--text-font);
    font-size: 1.3vw;
    height: 3vw;
  }

  .list-input__drop-list__list__item:hover {
    background-color: var(--accent-color);
    cursor: pointer;
  }

  .list-input__drop-list__button:focus + .list-input__drop-list__list {
    visibility: visible;
    opacity: 1;
  }

  .list-input__smart-input {
    grid-area: 1 / 2 / span 1 / span 1;
    position: relative;
    display: grid;
    place-items: center;
  }

  .list-input__smart-input__input {
    box-sizing: border-box;
    width: 100%;
    height: 100%;
    padding: 0 5%;
    transition: .15s;
    color: var(--text-color);
    font-size: 1.3vw;
    border-radius: 0 0.5vw 0.5vw 0;
    border: 0.04vw solid var(--border-color);
    background-color: transparent;
  }

  .list-input__smart-input__input::placeholder {
    color: var(--text-color-accent);
  }

  .list-input__smart-input__input:focus {
    border: 0.04vw solid var(--accent-color);
    box-shadow: 0 0 0 0.05vw var(--accent-color);
  }

  .list-input__smart-input__button-remove-text {
    height: 100%;
    width: 13%;
    margin-left: 87%;
    font-size: 1.3vw;
    position: absolute;
    color: var(--text-color);
    transition: .15s;
    opacity: 0;
    visibility: hidden;
  }

  .list-input__smart-input__button-remove-text:hover {
    color: var(--accent-color);
  }

  .list-input__smart-input__button-remove-text:active {
    transform: scale(.9);
  }

  .hidden {
    opacity: 0;
    visibility: hidden;
  }

  .visible {
    opacity: 1;
    visibility: visible;
  }
}

